  /* --- Radio Buttons & Checkboxes --- */
  .af-form input[type="checkbox"],
  .af-form input[type="radio"] {
	opacity: 0;
	position: absolute;
  }
  .af-form .check-content{
	display: inline-block;
	vertical-align: middle;
	font-family: 'Rubik Medium', Fallback, sans-serif;
	font-size: 15px;
	line-height: 27px;
	letter-spacing: 0;
	color: #767676;
  }
  .af-form input[type="checkbox"] + label{
	width: 16px;
	height: 16px;
	margin-right: 5px;
  }
  .af-form input[type="checkbox"] + label span {
	display: inline-block;
	width: 16px;
	height: 16px;
	margin: -3px 4px 0 0;
	vertical-align: middle;
	background: url(../images/box-empty.png) left top no-repeat;
	cursor: pointer;
  }
  .af-form input[type="checkbox"]:checked + label span {
	background: url(../images/box-checked.png) left top no-repeat;
  }
  .af-form input[type="radio"] + label span {
	display: inline-block;
	width: 16px;
	height: 16px;
	margin: -3px 4px 0 0;
	vertical-align: middle;
	background: url(../images/radio-empty.png) left top no-repeat;
	cursor: pointer;
  }
  .af-form input[type="radio"]:checked + label span {
	background: url(../images/radio-checked.png) left top no-repeat;
  }
  
  /* - White Check - */
  .af-form .white-check input[type="checkbox"] + label span {
	width: 27px;
	height: 26px;
	background: url(../images/box-checkbox-1.png) left top no-repeat;
  }
  .af-form .white-check input[type="checkbox"]:checked + label span {
	background: url(../images/box-checkbox-1-checked.png) left top no-repeat;
  }
  
  /* - Radio Button - */
  .af-form .white-check input[type="radio"] + label span {
	width: 30px;
	height: 30px;
	background: url(../images/box-radio-empty.png) left top no-repeat;
  }
  .af-form .white-check input[type="radio"]:checked + label span {
	background: url(../images/box-radio-checked.png) left top no-repeat;
  }
  
