  /* --- Plus Minus Input Field --- */
  .field-counter{
	width: 135px;
	height: auto;
	border-radius: 4px;
	z-index: 99;
	margin-bottom: 0;
  }
  .field-counter:before,
  .field-counter:after{
	display: block;
	content: "";
	clear: both;
  }
  .field-counter .counter-field{
	width: 76%;
	float: left;
	border: 1px solid #d7dfe8;
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
	text-align: left;
	font-family: 'Rubik Regular', Fallback, sans-serif;
	padding-left: 20px;
	padding-right: 20px;
	color: #2788e6;
	background: #f9f9f9;
  }
  .field-counter .counter-field > input.form-control{
	font-size: 15px;
	line-height: normal;
	letter-spacing: 0;
	border: none;
	box-shadow: none;
	display: block;
	border-radius: 0;
	height: 55px;
	padding: 0;
	color: #2788e6;
	background: #f9f9f9;
  }
  .field-counter .counter{
	width: 24%;
	float: left;
	display: block;
  }
  .field-counter .counter-up,
  .field-counter .counter-down{
	height: auto;
	padding: 5.5px 8px;
	border: none;
	line-height: normal;
	border-radius: 0;
	width: 100%;
	display: block;
	right: -1px;
	border: 1px solid #d7dfe8;
	z-index: 10;
	color: #2788e6;
	border-left: none;
	background: #f9f9f9;
  }
  .field-counter .counter .counter-up span.fa,
  .field-counter .counter .counter-down span.fa{
	left: auto;
	top: 1px;
  }
  .field-counter .counter-up:focus,
  .field-counter .counter-down:focus{
	outline: none;
	outline-offset: 0;
  }
  .field-counter .counter-up{
	border-bottom: 1px solid #cacaca;
	border-top-right-radius: 4px;
  }
  .field-counter .counter-up .fa{
	position: relative;
	top: 3px;
  }
  .field-counter .counter-down{
	border-bottom-right-radius: 4px;
	border-top: none;
  }
  .field-counter .counter-down .fa{
	position: relative;
	top: -3px;
  }
  
  
